<script setup>
import { ref, onBeforeMount, watchEffect } from "vue";
import { useToast } from "primevue/usetoast";
import { SlideInOut } from "vue3-transitions";

import { useRoute } from "vue-router";
import axios from "axios";

const route = useRoute();
const toast = useToast();
let tokenjwt = localStorage.getItem("token");

let dataadd = ref({});

let WithContracttoggleValue = ref(false);
const slideIn = (el, done) => {
  el.style.opacity = 0;
  el.style.transform = "translateX(-30px)"; // Slide to the left

  setTimeout(() => {
    el.style.transition = "all 0.3s";
    el.style.opacity = 1;
    el.style.transform = "translateX(0)";
  }, 10);

  el.addEventListener("transitionend", done);
};

// Function to handle slide-out animation for the first card
const slideOut = (el, done) => {
  el.style.transition = "all 0.3s";
  el.style.opacity = 0;
  el.style.transform = "translateX(-30px)"; // Slide to the left

  el.addEventListener("transitionend", done);
};

// Function to handle slide-in animation for the second card
const slideFromLeft = (el, done) => {
  el.style.opacity = 0;
  el.style.transform = "translateX(-30px)"; // Slide from the left

  setTimeout(() => {
    el.style.transition = "all 0.5s";
    el.style.opacity = 1;
    el.style.transform = "translateX(0)";
  }, 10);

  el.addEventListener("transitionend", done);
};

// Function to handle slide-out animation for the second card
const slideToRight = (el, done) => {
  el.style.transition = "all 0.0001s";
  el.style.opacity = 0;
  el.style.transform = "translateX(30px)"; // Slide to the right

  el.addEventListener("transitionend", done);
};
const changeyesuser = () => {
  dataadd.value.username = "";
  dataadd.value.pass = "";
};
const adduser = () => {
  let body = {
    fullname: dataadd.value.fullname,
    cin: dataadd.value.cin,
    tel: dataadd.value.tel,
    adresse: dataadd.value.adresse,
    email: dataadd.value.email,
    salaire: dataadd.value.salaire,
    poste: dataadd.value.poste,
    username: dataadd.value.username || null,
    pass: dataadd.value.pass || null,
  };
  axios
    .post(`${process.env.VUE_APP_HOST_BACKEND}/users/addemploye`, body, {
      headers: {
        Authorization: `${tokenjwt}`,
      },
    })
    .then((response) => {
      console.log(response.data);
      toast.add({
        severity: "success",
        summary: "Succes",
        detail: "Employé Ajouté",
        life: 3000,
      });
      WithContracttoggleValue.value = false;
      dataadd.value = {};
    })
    .catch(function (error) {
      console.log(error);
      toast.add({
        severity: "error",
        summary: "Erreur",
        detail: "Employé Non Ajouté",
        life: 3000,
      });
    });
};
</script>

<template>
  <Toast />
  <div class="grid" :class="{ 'justify-center': !WithContracttoggleValue }">
    <div class="col-12 lg:col-6">
      <transition @enter="slideIn">
        <div class="card p-fluid">
          <div class="field">
            <label>Tu veux que cette employé utilise cette platforme?</label>
            <ToggleButton
              v-model="WithContracttoggleValue"
              onLabel="Oui"
              offLabel="Non"
              @change="changeyesuser()"
            />
          </div>
          <div class="field">
            <label>Nom & Prenom</label>
            <InputText autocomplete="off" v-model.trim="dataadd.fullname" />
          </div>
          <div class="field">
            <label>Cin</label>
            <InputNumber
              :useGrouping="false"
              autocomplete="off"
              v-model.trim="dataadd.cin"
            />
          </div>
          <div class="field">
            <label>Telephone</label>
            <InputNumber
              :useGrouping="false"
              autocomplete="off"
              v-model.trim="dataadd.tel"
            />
          </div>
          <div class="field">
            <label>Adresse</label>
            <InputText autocomplete="off" v-model.trim="dataadd.adresse" />
          </div>
          <div class="field">
            <label>Email</label>
            <InputText
              type="email"
              autocomplete="off"
              v-model.trim="dataadd.email"
            />
          </div>
          <div class="field">
            <label>Salaire</label>
            <InputNumber
              :useGrouping="false"
              autocomplete="off"
              v-model.trim="dataadd.salaire"
            />
          </div>
          <div class="field">
            <label>Poste</label>
            <InputText autocomplete="off" v-model.trim="dataadd.poste" />
          </div>
          <div class="field" v-if="!WithContracttoggleValue">
            <Button
              label="Ajouter Un Employée"
              class="p-button-primary"
              @click="adduser"
              :disabled="
                !dataadd.fullname ||
                !dataadd.cin ||
                !dataadd.tel ||
                !dataadd.adresse ||
                !dataadd.email ||
                !dataadd.salaire ||
                !dataadd.poste
              "
            />
          </div>
        </div>
      </transition>
    </div>
    <transition @enter="slideFromLeft" @leave="slideToRight">
      <div v-if="WithContracttoggleValue">
        <div class="col-12 card-left">
          <div class="card p-fluid">
            <div class="field">
              <label>Nom D'utilisateur</label>
              <InputText autocomplete="off" v-model.trim="dataadd.username" />
            </div>
            <div class="field">
              <label>mot de passe</label>
              <InputText
                type="password"
                autocomplete="off"
                v-model.trim="dataadd.pass"
              />
            </div>
            <div class="field">
              <Button
                label="Ajouter Un Employée"
                class="p-button-primary"
                @click="adduser"
                :disabled="
                  !dataadd.fullname ||
                  !dataadd.cin ||
                  !dataadd.tel ||
                  !dataadd.adresse ||
                  !dataadd.email ||
                  !dataadd.salaire ||
                  !dataadd.poste ||
                  !dataadd.username ||
                  !dataadd.pass
                "
              />
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>
<style scoped>
@media screen and (min-width: 992px) {
  .input100 {
    width: 355% !important;
    display: block;
  }
}
.input100 {
  width: 100%;
  display: block;
  color: black;
  opacity: 1 !important;
}
.line-seperator {
  border: 3px solid #484bad;
  border-radius: 10px;
}
.justify-center {
  justify-content: center;
}
.card {
  transition: all 0.3s cubic-bezier(0, 1.86, 1, 2.23);
}
.card-left {
  width: 150% !important;
}
@media (min-width: 1025px) {
  .card-left {
    width: 150% !important;
  }
}
/* Tablet Mode */
@media (min-width: 768px) and (max-width: 1024px) {
  .card-left {
    width: 245% !important;
  }
}
/* Phone Mode */
@media (max-width: 767px) {
  .card-left {
    width: 136% !important;
  }
}
</style>
